import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import CardWithBack from '../../../components/CardWithBack/CardWithBack';
import { getAllRatingReviewByChefIdSaga } from '../../../store/actions';
import { Table } from '../../../components';

const tableFields = [
  {
    displayName: 'Order ID',
    key: 'orderNumber',
  },
  {
    displayName: 'User Name',
    key: 'userName',
  },
  {
    displayName: 'Vendor Rating',
    key: 'rating',
    className: 'active-green',
    isRating: true,
    ratingType: 'star',
  },
  {
    displayName: 'Quality',
    key: 'taste',
    isRating: true,
  },
  // {
  //   displayName: 'Temperature',
  //   key: 'temperature',
  //   isRating: true,
  // },
  {
    displayName: 'Packaging',
    key: 'packaging',
    isRating: true,
  },
  {
    displayName: 'Price Value',
    key: 'priceValue',
    isRating: true,
  },
  {
    displayName: 'Vendor Review',
    key: 'review',
  },
  {
    displayName: 'Logistics Rating',
    key: 'logisticRating',
    className: 'active-green',
    isRating: true,
    ratingType: 'star',
  },
  {
    displayName: 'Time',
    key: 'logisticTime',
    isRating: true,
  },
  {
    displayName: 'Food handling',
    key: 'logisticFoodHandling',
    isRating: true,
  },
  {
    displayName: 'Professionalism',
    key: 'logisticProfessionalism',
    isRating: true,
  },
  {
    displayName: 'Logistics Review',
    key: 'logisticReview',
  },
];

// eslint-disable-next-line no-unused-vars
const RatingDetail = props => {
  const [mounted, setMounted] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const {
    reviews: { list: allData, totalRecords },
    avgRating,
    restaurantName,
    restaurantEmail,
    retsaurantContact,
    totalRatingCount,
    errorMsg,
  } = useSelector(state => state.review);

  const dispatch = useDispatch();

  const findRatingLabel = number => {
    if (number <= 5 && number >= 4.51) {
      return 'Excellent';
    }
    if (number <= 4.5 && number >= 4) {
      return 'Good';
    }
    if (number <= 4 && number >= 3.5) {
      return 'Average';
    }
    return 'Opps';
  };

  const getData = (columnName, orderBy, search, skip, limit) => {
    const { id } = props.match.params;
    if (search && skip !== 0) {
      dispatch(
        getAllRatingReviewByChefIdSaga({
          columnName,
          orderBy,
          search,
          skip: 0,
          limit,
          chefId: id,
        }),
      );
    } else {
      dispatch(
        getAllRatingReviewByChefIdSaga({
          columnName,
          orderBy,
          search,
          skip,
          limit,
          chefId: id,
        }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('', 'desc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  const linkObj = {};

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <CardWithBack
      pageTitle="View Review"
      dataLoadeed={typeof 1 === typeof 2}
      errorMsg=""
    >
      <div className="border-solid  m-b-sm" id="top-0">
        <div className="row">
          <div className="col-md-12">
            <div className="row box-diff">
              <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                <div className="detail-title-a">
                  <p>
                    <span>Vendor Name:</span>
                    {restaurantName || '-'}
                  </p>
                  <p>
                    <span>Vendor Email Id: </span>
                    {restaurantEmail || '-'}
                  </p>
                  <p>
                    <span>Vendor Contact: </span>
                    {retsaurantContact || '-'}
                  </p>
                </div>
              </div>
              <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                <div className="detail-title-a">
                  <p>
                    <span>Vendor Rating:</span>
                    <div className="btn btn-white btn-bitbucket btn-rating">
                      <i className="fa fa-heart" />
                      {Number.parseFloat(avgRating).toFixed(1)}
                    </div>
                    <p style={{ marginTop: 5 }}>
                      {`${findRatingLabel(
                        Number.parseFloat(avgRating).toFixed(1),
                      )} 
                    (${totalRatingCount})`}
                    </p>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <h2 style={{ padding: 0, margin: 0, paddingTop: '10px' }}>
        Rating Detail
      </h2>
      <div className="coman-table m-t-md">
        <Table
          headers={tableFields}
          tableData={allData || null}
          pageCount={totalRecords?.count}
          getData={getData}
          link={linkObj}
          linkPropsName="_id"
          setSearchFlag={searchFlagSetter}
          errorMsg={errorMsg || 'Loading...'}
          // pageSizeProp={pageSize}
        />
      </div>
    </CardWithBack>
  );
};

export default SetTokenHeader(withRouter(RatingDetail), axios);
